<template>
  <div id="wrapper">
      <button v-on:click="next(`${move_to}`)" v-bind:style="styl" id="button" type="button">{{title}}</button>
  </div>
</template>

<script>
export default {
    name: 'Button',
    props: [
        'title',
        'styl',
        'move_to',
    ],
    methods: {
        next(arg) {
            arg = arg.toLowerCase();
                this.$router.replace({ name: `${arg}` });
        },
    },
}
</script>

<style lang="scss" scoped>
    #button {
        height: 45px;
        border: 2px white dashed;
        font-family: "Comic Sans MS";
        font-size: 26px;
        background-color: transparent;
        border-radius: 20px;
        color: white;
        outline:none;
    }

    #button:active {
        color: #FF4040;
        background-color: white;
    }
</style>
