<template>
    <div id="login">
        <span id="header">Please log in</span> <br />
        <div id="form">
            <span class="small-header">Username:</span>  <br />
            <input class="input" type="text" name="username" placeholder="Dzbaniula" v-model="input.username" />  <br />
            <span class="small-header">Password:</span>  <br />
            <input class="input" type="password" name="password" v-model="input.password" /> <br />
            <button id="button" type="button" v-on:click="login()">Let's go!</button>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'Login',
        data() {
            return {
                input: {
                    username: "",
                    password: ""
                }
            }
        },
        methods: {
            login() {
                if(this.input.username != "" && this.input.password != "") {
                    if(this.input.username == this.$parent.mockAccount.username && this.input.password == this.$parent.mockAccount.password) {
                        this.$emit("authenticated", true);
                        this.$router.replace({ name: "secure" });
                    } else {
                        console.log("The username and / or password is incorrect");
                    }
                } else {
                    console.log("A username and password must be present");
                }
            }
        }
    }
</script>

<style scoped>
    #login {
        margin-top: 117px;
        text-align: center;
        color: white;
        padding-bottom: 200px;
    }

    #header {
        font-size: 48px;
    }

    #form {
        font-size: 32px;
        margin-top: 117px;
    }

    .input {
        font-family: "Comic Sans MS";
        font-size: 26px;
        width: 288px;
        height: 45px;
        border: 2px white solid;
        background-color: transparent;
        border-radius: 20px;
        color: white;
        padding-left: 10px;
        padding-right: 10px;
        margin-top: 10px;
        outline:none;
    }

    ::placeholder {
        color: #e0e0e0;
        opacity: .2;
    }

    .small-header {
        margin-bottom: 10px;
    }

    #button {
        font-family: "Comic Sans MS";
        font-size: 26px;
        margin-top: 30px;
        width: 146px;
        height: 45px;
        border: 2px white dashed;
        background-color: transparent;
        border-radius: 20px;
        color: white;
        outline:none;
    }

    #button:active {
        color: #FF4040;
        background-color: white;
    }

    @media screen and (min-width: 1366px) {
        #login {
            padding-bottom: 0px;
        }

        #form {
            margin-top: 80px;
        }
    }

    @media screen and (min-width: 1920px) {
        #header {
            font-size: 82px;
        }

        .small-header {
            font-size: 42px;
        }

        .input {
            width: 420px;
            height: 65px;
        }

        #button {
            width: 235px;
            height: 70px;
            font-size: 40px;
            margin-top: 50px;
        }

        #form {
            margin-top: 100px;
        }
    }
</style>