<template>
    <VideoPlayer v-if="getWidth() === 375">
        <source src='../assets/vid/375.mp4' type='video/mp4'>
    </VideoPlayer>
    <VideoPlayer v-else-if="getWidth() === 1920">
        <source src='../assets/vid/1920.mp4' type='video/mp4'>
    </VideoPlayer>
    <VideoPlayer v-else-if="getWidth() === 1366">
        <source src='../assets/vid/1366.mp4' type='video/mp4'>
    </VideoPlayer>
</template>

<script>
import VideoPlayer from '../components/VideoPlayer.vue';

export default {
    name: 'movie',
    components: {
        VideoPlayer,
    },
    methods: {
            getWidth: function width() {
                //Get vertical screen resolution and play movie depending on it
                let wid = window.innerWidth;
                return wid;
            },
        },
}
</script>

