import Vue from 'vue'
import Router from 'vue-router'
import LoginComponent from "./views/login.vue"
import SecureComponent from "./views/secure.vue"
import After_yesComponent from "./views/after_yes.vue"
import After_noComponent from "./views/after_no.vue"
import MovieComponent from "./views/movie.vue"
import EndpageComponent from "./views/endpage.vue"

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: {
        name: "login"
      }
    },
    {
      path: "/login",
      name: "login",
      component: LoginComponent
    },
    {
      path: "/secure",
      name: "secure",
      component: SecureComponent
    },
    {
      path: "/after_yes",
      name: "after_yes",
      component: After_yesComponent
    },
    {
      path: "/after_no",
      name: "after_no",
      component: After_noComponent
    },
    {
      path: "/movie",
      name: "movie",
      component: MovieComponent
    },
    {
      path: "/endpage",
      name: "endpage",
      component: EndpageComponent
    },
  ]
})